/* 공통 */
.title {font-size: 35px; margin-bottom: 10px;
        font-weight: 700; padding-left: 30px;}


/* article1 */
.rowtwo {
        border: 1px solid #ccc; background: rgba(255,255,255,0.7);
        max-width: 900px; margin: 10px auto 0; border-radius: 50px; 
        position: absolute; left: 50%; text-align: center;
        top: 18%; transform: translate(-50%, -50%); 
        z-index: 99;}
.searchbox {display: flex; color: #000;}
.boxx {margin: 0 auto; border-radius: 50px; padding: 0 10px; }
.boxx:nth-child(1) {width: 25%;}
.boxx:nth-child(2) {width: 18%;}
.boxx:nth-child(3) {width: 25%;}
.boxx:nth-child(4) {width: 30%;}
.searchbox button {font-size: 25px; padding: 10px 0 0 ;
                   background: rgb(255, 56, 92); width: 50px; height: 50px; 
                   border-radius: 50%; color: #fff; margin: 13px 0;}
                   
.article1 {overflow: hidden; margin-bottom: 100px; position: relative;}
.slide {height: 55vw !important; background-size: cover; background-repeat: no-repeat;
        background-position: center; position: relative;}
.sdtxt {position: absolute; left: 50%; text-align: center;
        top: 65%; transform: translate(-50%, -50%); color: #fff;
        font-size: 45px; font-weight: 700;}
.sdtxt button {background: #fff; padding: 15px 33px; transition: all 0.5s;
                font-size: 18px; border-radius: 30px;}
.sdtxt button:hover {background: #ccc;}
/* .slick-arrow {position: absolute; top: 50%; transform: translateY(-50%);
               background: none; padding: 10px; z-index: 99999; width: 40px; 
               height: 40px; color:#555; } */
/* .slick-prev {left: 10px;}
.slick-next {right: 10px;}   */
.slick-dots {position: absolute; bottom: 10px; left: 50%; transform: translate(-65%); }
.slick-dots li {display: inline-block; padding: 0 5px;}
.slick-dots button {width: 20px; height: 5px; text-indent: -999999px; 
                    background: rgba(255, 255, 255, 0.5); overflow: hidden;}
.slick-dots li.slick-active button {background:#fff;}


/* article2 */
.article2 {margin-top: 50px; margin-bottom: 50px;}
.citybox {padding: 0 0px 70px 0;}
.citybox ul {display: flex; padding: 0 10px; color: #fff; }
.citybox ul li {padding-bottom: 0px; margin-right: 20px;
                padding-bottom: 70px; border-radius: 30px;}
                .citybox ul li:nth-child(4) {margin-right: 0;}
.citybox ul li:nth-child(1) {background: #cc2d4a;}
.citybox ul li:nth-child(2) {background: #bc196d;}
.citybox ul li:nth-child(3) {background: #d93a30;}
.citybox ul li:nth-child(4) {background: #dd3150;}
.citybox ul li h2 {font-size: 28px; padding-left: 10px;}
.citybox ul li p {padding: 10px; font-size: 18px;}
.citybox ul li img {border-radius: 30px 30px 0 0;}
.citybox li > div {line-height: 0.5;}

/* article3 */
.article3 {margin-top: 50px; margin-bottom: 100px;}
.exbox {display: flex; }
.exbox > div {margin: 0 10px; display: flex; position: relative;}
.exbox > div > img {border-radius: 30px; margin: 0 auto;}
.exbox div:nth-child(1){padding-right: 20px;}
.exbox div:nth-child(2){padding-left: 20px;}
.exbox button {padding: 15px 27px; background: #f7f7f7; 
               border-radius: 10px; font-size: 16px;
               transition: all 0.5s}
.exbox button:hover {background: #ccc}
.extxtbox {position: absolute; top: 70px; left: 40px; }
.exbox div:nth-child(2) .extxtbox {margin-left: 20px;} 
.extxtbox h2 {color: #fff; font-weight: 600; font-size: 45px;
              line-height: 1.3;}


/* article4 */
.article4 {overflow: hidden;  padding: 0 10px;
           margin-top: 170px; margin-bottom: 50px;}
.article4 > .row {position: relative; overflow: hidden;}
.article4 > .row > div {padding: 0 10px;}
.article4 img {border-radius: 30px; }
.asktxtbox {position: absolute; top: 65px; left: 100px; right: 0; bottom: 0;}
.asktxtbox button {padding: 15px 27px; background: #f7f7f7; 
                  font-size: 16px; border-radius: 10px; 
                   transition: all 0.5s;}
.asktxtbox button:hover {background: #ccc}
.asktxtbox h2 {color: #fff; font-weight: 600; font-size: 48px;
               line-height: 1.5;}